<template>
	<view class="" style="display: flex;flex-flow: column nowrap;align-items: center;width: 750upx; ">
		
		<navigationview :current="current" :values="items" v-on:clickItem="onClickItem" :list_id="list_id"></navigationview>



		<view class="jichukuangjia" :style="'height:'+height+'px'  " v-show="current === 0">
			<mpvue-echarts :echarts="echarts" :onInit="pieInit" canvasId="pie" />
			<view class="" style="position: absolute;top:100upx ; left: 10upx; font-size: 30upx; flex-flow: column; height: 100upx; justify-content: space-between;">
				<view class="icontext" @click="fangda">&#xe613;</view>
				<view class="icontext" @click="suoxiao">&#xe621;</view>
			</view>

		</view>
		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height+'px' " v-if="current === 1">
			<handleview  :card_id="luyouData[list_id].card_id"></handleview>
		</scroll-view>


	</view>
</template>
<script>
	import navigationview from '../../../components/navigation.vue';
	import * as echarts from '../../../components/echarts/echarts.tree.min.js';
	import mpvueEcharts from '../../../components/mpvue-echarts/src/echarts.vue';
	import handleview from '../../../components/handle-view.vue';
	// import * as echartsoption from '../../../common/echart_list.js';
	// import luyouData from '../../../common/luyou.js';

	var pieChart = null;

	export default {


		data() {
			return {
				luyouData: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
					{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
				],
				list_id: null,
				items: [
					'地图',
					'改进',
				],
				height: 0,
				current: 0,
				item_getdata_list: null,
				fangdabeishu: 1,
				echarts,
			}
		},

		components: {
			handleview,
			navigationview,
			mpvueEcharts
		},
		onLoad: async function(option) {

			this.height = uni.getSystemInfoSync().windowHeight - 70 * (uni.getSystemInfoSync().windowWidth / 750);
			console.log("list_id:" + option.list_id)
			this.list_id = option.list_id;
			let aa = await this.get_hot_card_list()
			setTimeout(() => this.deal_with_data(), 500);
		},

		methods: {

			pieInit: function(canvas, width, height, data) {
				var that = this;
				uni.getStorage({
					key: 'allcitys_map_json',
					success: function(res) {
						console.log("getStorage_map_json", res.data);
						echarts.registerMap('chinacity', res.data);
					}
				});
				pieChart = echarts.init(canvas, null, {
					width: width,
					height: height
				})
				canvas.setChart(pieChart)
				return pieChart
			},

			onClickItem(index) {
				if (this.current !== index) {
					this.current = index;
				}
			},

			

			deal_with_data: async function() {
				const that = this;
				let getdataresult = await that.getData_list();
				pieChart.setOption(this.chose_option());
				pieChart.on('click', function(param) {
				
					console.log("点击事件:", param.data)
					// 					that.department_name = param.data.name;
					// 					that.department_id = param.data.value;
				})

			},

			fangda() {
				this.fangdabeishu += 0.3;
				pieChart.setOption(this.chose_option())

			},
			suoxiao() {
				this.fangdabeishu -= 0.3;
				pieChart.setOption(this.chose_option())
			},
			chose_option() {
				var option = this[this.luyouData[this.list_id].getechartfunction](this.item_getdata_list, this.fangdabeishu,
					this.getNowFormatDate())

				return option
			},


			getData_list() {
				return new Promise((res) => {
					uni.request({
						url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
						method: "POST",
						data: {
							'form': this.luyouData[this.list_id].getdata_title,
							'data1': this.getNowFormatDate(),
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						fail: (e) => {
							res(e);
						},
						success: (ret) => {
							if (ret.statusCode !== 200) {
								console.log("请求失败:", ret)
							} else {
								console.log("下载数据:", ret.data)
								console.log("下载时间",this.getNowFormatDate())
								this.item_getdata_list = ret.data.data;

								// console.log("lists:", ret.data.data)
							}
							res(ret);
						}
					});
				})

			},



			getNowFormatDate() {
				var curDate = new Date();
				var startDate = new Date(curDate.setDate(curDate.getDate() + 2));
				var seperator1 = "-";
				var year = startDate.getFullYear();
				var month = startDate.getMonth() + 1;
				var strDate = startDate.getDate();
				if (month >= 1 && month <= 9) {
					month = "0" + month;
				}
				if (strDate >= 0 && strDate <= 9) {
					strDate = "0" + strDate;
				}
				var currentdate = year + seperator1 + month + seperator1 + strDate;
				return currentdate;
			},









