						trigger: 'item',
						formatter: '{b}:{c}°C'
					},

					legend: {
						selectedMode: 'single',
						orient: 'vertical',
						y: 'bottom',
						x: 'right',
						data: ['最高温', '最低温', '温度差'],
						//					itemWidth:40,itemHeigh:40,
						textStyle: {
							color: '#fff',
							fontSize: 18
						}
					},
					visualMap: {
						min: -5,
						max: 35,

						text: ['35°C', '-5°C'],
						//	calculable: true,
						inRange: {
							color: ['#50a3ba', '#eac736', '#d94e5d'],
							//						 symbolSize: [8, 10]
						},
						textStyle: {
							color: '#fff'
						}
					},
					geo: {
						map: 'chinacity',
						show: true,
						roam: true,
						zoom: fangdabeishu,
						scaleLimit: {
							min: 0.7,
							max: 1.8
						},
						label: {
							emphasis: {
								show: false
							}
						},
						itemStyle: {
							normal: {
								areaColor: '#323c48',
								borderColor: '#111'
