								emphasis: {
									show: false
								}
							},
							itemStyle: {
								emphasis: {
									borderColor: '#fff',
									borderWidth: 1
								}
							}
						},
						{
							name: '最低温',
							type: 'scatter',
							coordinateSystem: 'geo',
							data: that.convertData(tianqidata, 'low_tem'),
							symbolSize: 8,
							label: {
								normal: {
									show: false
