									borderWidth: 1
								}
							}
						}


					]


				}
			},

			convertData(array, type) {
				var geoCoordMap = this.get_geoCoordMap();
				var res = [];
				if (array != null) {
					for (var i = 0; i < array.length; i++) {
						var geoCoord = geoCoordMap[array[i].province];
						if (geoCoord) {
							var arr = geoCoord.concat(array[i][type]);
							res.push({
								name: array[i].province,
								value: arr
							});
						}
					}
				}
				return res;
			},

			convertData_temcha(array) {
				var geoCoordMap = this.get_geoCoordMap();
				var res = [];
				if (array != null) {
					for (var i = 0; i < array.length; i++) {
						var geoCoord = geoCoordMap[array[i].province];
						if (geoCoord) {
							var cha = parseFloat(array[i]['high_tem']) - parseFloat(array[i]['low_tem'])
							// console.log(cha)
							// console.log(array[i]['high_tem'])
							var arr = geoCoord.concat(cha);
							res.push({
								name: array[i].province,
								value: arr
							});
						}
					}
				}
				return res;
			},


