			convertData_fenxiang(array, type) {
				var geoCoordMap = this.get_geoCoordMap();
				var res = [];
				for (var i = 0; i < array.length; i++) {
					var geoCoord = geoCoordMap[array[i]['province']];
					if (geoCoord && array[i]['wind_direction'] == '北' && type == 1) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['wind_direction'] == '西北' && type == 2) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['wind_direction'] == '西' && type == 3) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['wind_direction'] == '西南' && type == 4) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['wind_direction'] == '南' && type == 5) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['wind_direction'] == '东南' && type == 6) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['province'] == '东' && type == 7) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['wind_direction'] == '东北' && type == 8) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
					if (geoCoord && array[i]['wind_direction'] == '无持续风向' && type == 9) {
						res.push({
							name: array[i]['province'],
							value: geoCoord.concat(array[i]['wind_speed'])
						});
					}
				}
				return res;
			},




